import React, { Component } from 'react';
import './App.css';
import styled,{createGlobalStyle} from "styled-components";
import LeftList from './menu/leftList'
import MainList from './menu/mainList'
import RightList from './menu/rightList'
const height = window.innerHeight;

const GlobalStyle = createGlobalStyle`  
  html {
    height: 100vh; 
  }
  body {
    width :100%;    
    height: 100vh;  
    margin : 0 auto;
    padding : 0px;
    background-color :#000;
    color : white
  }
`
const Container = styled.div`
  display : flex;
  flex-direction: column;
  margin: 0 auto;
`
const Header = styled.header`
`
const TopMenu = styled.header`
  display : ${props=>props.display};
  justify-content: center;
  align-items: center; 

`
const TopMenuList = styled.header` 
  padding : 0 30px 10px 30px;
  text-align : center;
`
const TitleName = styled.p`
  font-weight : ${props=>(props.title === "top" ? 1000:700)};
  font-size : ${props=>(props.title === "top" ? "30px":"20px")};
  text-align : center;
`
class App extends Component {
  constructor(props){
    super()
    this.state={
      ...props,
      commandList : [{cmdName : "CMD"},{cmdName : "LIST"}],
      cmdValue:"",
      isTopMenuVisible:"none"
    }
  }
  _cmdInput = (inputText) =>{
    switch (inputText){
      case "showmenu" :
        this.setState({
          isTopMenuVisible:"flex"
        })
      break;
      case "hidemenu" :
        this.setState({
          isTopMenuVisible:"none"
        })
      break;
      default:
        alert("명령어 없음")
    }
    
  }
  render() {
    return (
      <Container>
        <GlobalStyle />
        <Header>
          <TitleName title="top">HOME PAGE</TitleName>
          <TitleName title="sub">Kim Jong Woo</TitleName>
        </Header> 
        <TopMenu display ={this.state.isTopMenuVisible}>
          <TopMenuList>MENU 1</TopMenuList>  
          <TopMenuList>MENU 2</TopMenuList>  
        </TopMenu>  
        <MainList cmdInput = {this._cmdInput}></MainList>
        
      </Container>

    );
  }
}

export default App;
