import React, { Component } from 'react';
import styled,{createGlobalStyle} from "styled-components";
import { list } from 'postcss';
const height = window.innerHeight;
const MainContainer = styled.div`
  display : flex;
  flex :3;
  flex-direction: column;
  height : ${height/2}px;
  justify-content: center;
  align-items: center; 
  @media ( max-width: 480px ) {
    float: none;
    width: auto;
  }
`
const CmdContainer = styled.div`
  display : flex; 
`
const CmdInput = styled.input`  
  border : 1px solid #000;
  background-color :#000;
  color:#ffffff;
  margin : 0px 0px 0px 10px
`
const CmtTitle = styled.a`
  font-weight : 700;
  font-size : 20px;
  text-align : center;
`
class MainList extends Component {
  constructor(props){
    super()
    this.state={
      ...props,
      cmdValue : "",
      cmdList :[{listName : "showmenu", listAction :"메뉴를 보여주는 기능"},{listName : "hidemenu", listAction :"메뉴를 숨겨주는 기능"}]
    }
  }
  componentDidMount(){
    this.cmdInput.focus();
  }
  _sendInputText=(e)=>{
    if(e.keyCode === 13){
      this.setState({
        cmdValue : ""
      })
      this.props.cmdInput(this.state.cmdValue)
    }
  }
  render() {
    return (     
      <MainContainer>
        <CmdContainer>
            <CmtTitle>Command</CmtTitle>
            <CmdInput 
              value = {this.state.cmdValue}
              ref={ref=>{this.cmdInput = ref}}
              placeholder ="input your cmd ..."
              onChange ={(e)=>{this.setState({cmdValue : e.target.value})}}
              onKeyDown ={(e)=>{this._sendInputText(e)}}>
            </CmdInput>    
          {/* <h5>
            {"cmdList : "}
            {this.state.commandList.map((item)=>{
              return item.cmdName+"  "
            })}
          </h5> */}
        </CmdContainer>
      <h4>명령어 리스트</h4>
      <h5>{this.state.cmdList.map((item)=>{return item.listName +": "+ item.listAction + ", "})}</h5>
     </MainContainer>
    );
  }
}

export default MainList;
